import { registerMicroApps, start } from "qiankun";
import ReactDOM from "react-dom/client";
import { BrowserRouter } from "react-router-dom";
import App from "./App";
import "./index.css";
import reportWebVitals from "./reportWebVitals";

registerMicroApps([
  {
    name: "react01", // name  ID
    entry: process.env.REACT_APP_MICRO_APP_FIRST!, // IP
    container: "#container", //  DOM DOM
    activeRule: "qiankun-demo/subapp/react01", // 
  },
  {
    name: "react02",
    entry: process.env.REACT_APP_MICRO_APP_SECOND!,
    container: "#container", // 
    activeRule: "qiankun-demo/subapp/react02",
  },
]);

start();

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);

root.render(
  // <React.StrictMode>
  <BrowserRouter>
    <App />
  </BrowserRouter>
  // </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
